import React from 'react';
import PropTypes from 'prop-types'

export default class DetailMusic extends React.Component{
    
    render(){

        var content = this.props.content;
        console.log(content);
        
        return  (<div className="detail-item detail-music">
        <div className="music item">
    
            <img className="item-image" src={content.image} />
            <p className="item-title">: {content.title}
                <span className="item-tag"> {content.tags.map((tag, index)=>{
                    return <span key={index} index={index}>{tag}</span>
                })} </span>   
            </p>
            
            <p className="item-author">{content.author}</p>
            <p className="item-publisher">{content.publisher.join(' ')}</p>
            <p className="item-pubdate">{content.pubdate}</p>
            <p className="item-rating">{content.rating}</p>
        </div>
        <div>
            <h2></h2>
            <h2></h2>
            <p>{content.title}</p>
        </div>
    </div>)
    }
}


DetailMusic.propTypes = {
    content: PropTypes.object
}