import React from 'react'
import PropTypes from 'prop-types'

export default class DetailBook extends React.Component {
  render () {
    var content = this.props.content

   
    return (<div className="detail-item detail-book">
        <div className="book item">

            <img className="item-image" src={content.image} />
            <p className="item-title">: {content.title}</p>

            <p className="item-author">{content.author}</p>
            <p className="item-publisher">{content.publisher}</p>
            <p className="item-pubdate">{content.pubdate}</p>
            <p className="item-rating">{content.rating}</p>
            <p className="item-price">:{content.price}</p>
            <p className="item-tag"> {content.tags.map((tag, index)=>{
                if(tag.length > 4){
                    return null;
                }
                return <span key={index} index={index}>{tag}</span>
            })} </p>
        </div>
        <div>
            <h2></h2>
            <p>{content.catalog.slice(0, 250)}...</p>
            <h2></h2>
            <p>{content.summary.slice(0, 250)}...</p>
        </div>
    </div>)
  }
}

DetailBook.propTypes = {
  content: PropTypes.object
}
